import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import AdManPage from '../system/AdManPage';

function LinkImgs({ImgNum}) {
    const LinkImg = AdManPage.links.PortFolilLinks;
    const LinkImgTexA = "https://img.youtube.com/vi/";
    const LinkImgTexB = "/mqdefault.jpg"
    const ImgUrl = LinkImgTexA+(LinkImg[ImgNum].slice(-11))+LinkImgTexB;

    return(
        <img src={ImgUrl} height="100%" width="100%" alt={ImgUrl}></img>
    )
}

function Portfolio (){
    const WorkList= AdManPage.links.PortFolilLinks;
    const poplist = [];
    for (const [index] of WorkList.entries()) {
        poplist.push(
            <PortfolioLinks key={index}>
                <Link to={{
                            pathname: `/PortfolioLink/${index}`
                        }}>
                    <LinkImgs ImgNum={index}></LinkImgs>
                </Link>
            </PortfolioLinks>
        )
      }

    return(
        <PortfolioContainer>
            <PortfolioContainerBox>
                {poplist}
            </PortfolioContainerBox>
        </PortfolioContainer>
    )
}

const PortfolioContainer = styled.section`
width: 100%;
background-color: rgb(0, 0, 0);
padding: 0 0 30px 0;

`;

const PortfolioContainerBox = styled.div`
display: flex;
flex-wrap: wrap;
width: 100%;
margin: auto;
@media only screen and (min-width: 640px){
    width: 80%;

}
@media only screen and (min-width: 1200px){
    width: 1200px;

}

`;

const PortfolioLinks = styled.div`
width: 33.3333%;

@media only screen and (min-width: 640px){
    width:20%;
}
@media only screen and (min-width: 1200px){

}
`;

export default Portfolio;