function CopyToClip(e) {
    const emailAdd = AdManPage.ContactPageLanguage["kr"].mail;
    const tempElement = document.createElement("textarea");

    document.body.appendChild(tempElement);
    tempElement.value = e;
    tempElement.select();
    document.execCommand('copy');
    document.body.removeChild(tempElement);
    
    if (e === emailAdd) {
        alert( "Eamil이" + AdManPage.ContactPageLanguage["kr"].copied);
    } else {
        alert( "주소가" + AdManPage.ContactPageLanguage["kr"].copied);
    };
  }

