import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import AdManPage from '../system/AdManPage';

function LinkImgs({ImgNum}) {
    const LinkImg = AdManPage.links.Mainlinks;
    const LinkImgTexA = "https://img.youtube.com/vi/";
    const LinkImgTexB = "/mqdefault.jpg"
    const ImgUrl = LinkImgTexA+(LinkImg[ImgNum].slice(-11))+LinkImgTexB;
    return(
        <img src={ImgUrl} height="100%" width="100%" alt={ImgUrl}></img>
    )
}

function Works (){
    const WorkList= AdManPage.links.Mainlinks;
    const poplist = [];
    for (const [index] of WorkList.entries()) {
        poplist.push(
            <WorksLinksLineLink key={index}>
                <Link to={{
                            pathname: `/WorkLists/${index}`
                        }}>
                    <LinkImgs ImgNum={index}></LinkImgs>
                </Link>
            </WorksLinksLineLink>
        )
      }

    return(
        <WorksContainer>
            <WorksTextTop>
                <p>WORKS</p>
            </WorksTextTop>
            <WorksLinks>
                <WorksLinksLine>
                    {poplist}
                </WorksLinksLine>
            </WorksLinks>
        </WorksContainer>
    )
}

const WorksContainer = styled.section`
margin: 80px 0;
width: 100%;

@media only screen and (min-width: 640px){
    margin: 200px 0;
}
  
`;

const WorksTextTop = styled.div`
font-size: 1.5rem;
letter-spacing: 5px;
padding: 20px 0;
color: white;
`;

const WorksLinks = styled.div`
display: flex;
margin: 0 auto;
`;

const WorksLinksLine = styled.div`
display: flex;
flex-wrap: wrap;
width: 100%;
margin: auto;

@media only screen and (min-width: 640px){
    width: 500px;
  }
  
`;

const WorksLinksLineLink = styled.div`
width: 33.3333%;

`;

export default Works;