import React from 'react';
import {HashRouter, Route} from 'react-router-dom';
import MainPage from './routes/MainPage';
import WorksPage from './routes/WorksPage';
import ContactPage from './routes/ContactPage';
import Header from '../system/Header';
import Footer from '../system/Footer';
import WorkLists from '../system/WorkLinks';
import PortfolioLink from '../system/PortfolioLink';


function App() {
  return (
    <HashRouter>
      <Header></Header>
      <Route path="/" exact component={MainPage}></Route>
      <Route path="/WorksPage" exact component={WorksPage}></Route>
      <Route path="/ContactPage" exact component={ContactPage}></Route>
      <Route path="/WorkLists/:name" component={WorkLists}></Route>
      <Route path="/PortfolioLink/:name" component={PortfolioLink}></Route>
      <Footer></Footer>
    </HashRouter>
  )
}

export default App;
