import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import AdManPage from '../system/AdManPage';

function WorkLists({ match, location, history }){
    const ImgNum = match.params.name;
    const WorkList= AdManPage.links.Mainlinks[ImgNum];
    const uTubeOp= "?rel=0&autoplay=1"

    return(
        <WorkListsCon>
            <LinkBox>
                <iframe width="560" height="315" src={WorkList+uTubeOp} title={WorkList} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            </LinkBox>
            <BackBox>
                <BackBox onClick={() => history.goBack()}>X</BackBox>
            </BackBox>
        </WorkListsCon>
    )
}

const WorkListsCon = styled.div`
position: absolute;
width: 100%;
height: 110%;
margin: -50px 0 0 0;
display: flex;
flex-direction: column;
background-color: rgb(0, 0, 0);

`;

const LinkBox= styled.div`
margin: 100px auto 0 auto;
`;

const BackBox = styled.div`
font-size: 3rem;
color: rgb(255, 255, 255);
margin: 100px auto;
`;

export default WorkLists;